nt:-9999px;\n      height:0;\n      line-height:0;\n      font-size:0;\n      overflow:hidden;\n  }\n  /*----------------------------------------- 2) Preloader --------------------------------------------------*/\n  #preloader {\n    position: fixed;\n    top: 0;\n    left: 0;\n    right: 0;\n    bottom: 0;\n    background-color: #fff;\n    z-index: 999999;\n  }\n  #status {\n    width: 200px;\n    height: 200px;\n    position: absolute;\n    left: 50%;\n    top: 50%;\n    background-image: url(../../../../assets/image/loader.gif);\n    background-repeat: no-repeat;\n    background-position: center center;\n    text-align: center;\n    margin: -40px 0 0 -100px;\n  }\n  #status img {\n    text-align: center;\n    margin-top: -80px;\n  }\n  \n  /*----------------------------------------- 3) Helper --------------------------------------------------*/\n  .no-pad {padding: 0 !important; margin: 0 !important;}\n  .pad-80 {padding-top: 80px;}\n  .mar-t-50{margin-top: 50px !important;}\n  .mar-tm-10{ margin-top: -10px;}\n  \n  .white-col{\n    background: #ffffff;\n    padding: none;\n  }\n  \n  /*---- span -----*/\n  .title-small span { position: relative;}\n  .title-small span:after {\n    content: \"\";\n    position: absolute;\n    bottom: -20px;\n    height: 3px;\n    background: #DEDEDE;\n    width: 28px;\n    left: 0;\n  }\n  .title-small-center span {position: relative;}\n  .title-small-center span:after {\n    content: \"\";\n    display: block;\n    margin: 0 auto;\n    margin-top: 20px;\n    height: 3px;\n    background: #DEDEDE;\n    width: 28px;\n    left: 0;\n  }\n  /*----- background ------*/\n  .grey-bg {\n    background: #f6f6f6;\n    padding: 120px 0;\n    display: block;\n  }\n  .white-bg {\n    background: #ffffff;\n    padding: 120px 0;\n    display: block;\n  }\n  /*-------- content --------*/\n  .content-detail {\n    font-weight: 400;\n    margin-top: 70px;\n    line-height: 30px;\n    font-size: 14px;\n    letter-spacing: 0.4px;\n  }\n  .content-details {\n    font-weight: 400;\n    margin-top: 40px;\n    line-height: 30px;\n    font-size: 14px;\n  }\n  .pad-bottom {\n    padding-bottom: 80px;\n  }\n  .text-detail {\n    margin-top: 30px;\n    color: #5f5f5f;\n  }\n  \n  .listing-item{\n    list-style: none;\n    display: inline-block;\n    width: 100%;\n    padding: 0;\n    margin: 0;\n  }\n  \n  /*----------------------------------------- 4) Header --------------------------------------------------*/\n  .header {\n    width: 100%;\n    z-index: 9999;\n  }\n  .for-sticky {\n    position: fixed;\n    z-index: -1;\n    bottom: 0;\n    width: 100%;\n    height: 50px;\n    opacity: 0.8;\n    z-index: 1;\n    top: 0;\n    left: 0;\n    border-bottom: 1px solid #f1f1f1;\n    display: block;\n    padding: 0px 15px 5px 15px;\n    background: #ffffff;\n    -webkit-transition: all 0.3s;\n    -moz-transition: all 0.3s;\n    -ms-transition: all 0.3s;\n    -o-transition: all 0.3s;\n    transition: all 0.3s;\n  }\n  .logo-nav {\n    padding: 6px 0 10px 0;\n    -webkit-transition: all 0.3s;\n    -moz-transition: all 0.3s;\n    -ms-transition: all 0.3s;\n    -o-transition: all 0.3s;\n    transition: all 0.3s;\n    width: 45px;\n    display: block;\n  }\n  .opacity-nav {\n    opacity: 0.8;\n    z-index: 2;\n  }\n  /*----------- panel ----------*/\n  .content-wrap {\n    -webkit-overflow-scrolling: touch;\n    height: 100%;\n    width: 100%;\n  }\n  .content {\n    position: absolute;\n    width: 100%;\n    height: 100%;\n  }\n  .content::before {\n    position: fixed;\n    top: 0;\n    left: 0;\n    z-index: 10;\n    width: 100%;\n    height: 100%;\n    background: rgba(0, 0, 0, 0.3);\n    content: '';\n    opacity: 0;\n    -webkit-transform: translate3d(100%, 0, 0);\n    transform: translate3d(100%, 0, 0);\n    -webkit-transition: opacity 0.4s, -webkit-transform 0s 0.4s;\n    transition: opacity 0.4s, transform 0s 0.4s;\n    -webkit-transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1);\n    transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1);\n  }\n  /* Menu Button */\n  .menu-button {\n    width: 34px;\n    height: 45px;\n    position: fixed;\n    right: 0;\n    margin-top: 13px;\n    margin-right: 35px;\n    z-index: 1000;\n    padding: 0;\n    cursor: pointer;\n    background: transparent;\n    border: none;\n  }\n  .menu-button span {\n    display: block;\n    position: absolute;\n    height: 1px;\n    width: 100%;\n    background: #2f2f2f;\n    border-radius: 9px;\n    opacity: 1;\n    left: 0;\n  }\n  .menu-button span:nth-child(1) {top: 0px;}\n  \n  .menu-button span:nth-child(2) {top: 9px;}\n  \n  .menu-button span:nth-child(3) {top: 18px;}\n  .menu-button:hover {opacity: 0.6;}\n  /* Close Button */\n  .close-button {\n    width: 1em;\n    height: 1em;\n    position: absolute;\n    right: 1em;\n    top: 1em;\n    overflow: hidden;\n    text-indent: 1em;\n    font-size: 0.75em;\n    border: none;\n    background: transparent;\n    color: transparent;\n  }\n  .close-button::before,\n  .close-button::after {\n    content: '';\n    position: absolute;\n    width: 3px;\n    height: 100%;\n    top: 0;\n    left: 50%;\n    background: #bdc3c7;\n  }\n  .close-button::before {\n    -webkit-transform: rotate(45deg);\n    transform: rotate(45deg);\n  }\n  .close-button::after {\n    -webkit-transform: rotate(-45deg);\n    transform: rotate(-45deg);\n  }\n  /* Menu */\n  .menu-wrap {\n    position: fixed;\n    z-index: 1001;\n    width: 300px;\n    height: 100%;\n    right: 0;\n    float: right;\n    background: #2f2f2f;\n    padding: 2.5em 1.5em 0;\n    -webkit-transform: translate3d(300px, 0, 0);\n    transform: translate3d(300px, 0, 0);\n    -webkit-transition: -webkit-transform 0.4s;\n    transition: transform 0.4s;\n    -webkit-transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1);\n    transition-timing-function: cubic-bezier(0.7, 0, 0.3, 1);\n  }\n  .menu,\n  .menu-list {\n    height: 100%;     \n    overflow-x: auto;\n  }\n  .menu-list {\n    -webkit-transform: translat