nite;\n    animation-play-state: running;\n  }\n  .mouse-icon .scroll {\n    position: relative;\n    border-radius: 10px;\n    background: #2f2f2f;\n    width: 4px;\n    height: 8px;\n    top: 4px;\n    margin-left: auto;\n    margin-right: auto;\n  }\n  \n  @-webkit-keyframes scrolling  {\n    0%   { top:2px;  opacity: 0;}\n    30%  { top:8px; opacity: 1;}\n    100% { top:16px; opacity: 0;}\n  }\n  \n  @keyframes scrolling  {\n    0%   { top:2px;  opacity: 0;}\n    30%  { top:8px; opacity: 1;}\n    100% { top:16px; opacity: 0;}\n  }\n  \n  \n  /*----- typed effect ---*/\n  .typed-cursor{ opacity: 0;}\n  \n  \n  /*----------------------------------------- 7) Sections --------------------------------------------------*/\n  /*---------- a. portfolio -----------*/\n  ul.portfolio-image {\n    list-style: none;\n    position: relative;\n    width: 100%;\n    padding: 0;\n    margin: 0;\n  }\n  ul.portfolio-image li {\n    float: left;\n    display: block;\n    list-style: none;\n    position: relative;\n    display: inline-block;\n    overflow: hidden;\n    padding-top: 46%;\n  }\n  ul.portfolio-image li:hover {\n    cursor: pointer;\n  }\n  ul.portfolio-image li:hover img {\n    -webkit-filter: grayscale(20%);\n    transform: scale(1.15);\n  }\n  ul.portfolio-image li:hover .image-bg {opacity: 1;}\n  ul.portfolio-image li:hover .description-wrap {\n    background: rgba(186, 186, 186, 0.6);\n  }\n  ul.portfolio-image li img {\n    position: absolute;\n    width: 100%;\n    height: 100%;\n    left: 0;\n    top: 0;\n    -webkit-filter: grayscale(100%);\n    transition: filter, transform, 0.5s;\n  }\n  ul.portfolio-image li .decription-wrap {\n    position: absolute;\n    width: 100%;\n    height: 100%;\n    top: 0;\n    left: 0;\n    background: rgba(242, 242, 242, 0.4);\n    padding: 20px;\n    transition: background, 0.5s;\n  }\n  ul.portfolio-image li .image-bg {\n    color: #2f2f2f;\n    background: #ffffff;\n    background: rgba(255, 255, 255, 0.9);\n    font-size: 